export default {
	data() {
		return {
			imei:null,
			devid:'43132880-90cb-45e7-b4f9-f3f0d1670a82',
			btnAddDisable:true,
			btnInfoDisable:true
		}
	},
	onLoad() {

	},
	methods: {
		scanDev(){
			uni.scanCode({
				success: (res) => {
					var result;
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					if(res.result.length>15){
						result = res.result.substring(0,15);
					}else{
						result = res.result;
					}
					console.log('result：' + result);
					this.imei = result;
					this.btnAddDisable = false;
				},
				fail: (err) => {
					console.log(err);

					uni.getSetting({
						success: (res) => {
							let authStatus = res.authSetting['scope.camera'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: this.i18n.content_note.text_app_name+'需要使用您的相机，请在设置界面打开相关权限',
									success: (res) => {
										if (res.confirm) {
											uni.openSetting()
										}
									}
								})
							}
						}
					})

				}
			});
		},
		imeiInput(e){
			console.log("imeiInput");
			this.imei = e.target.value;
			if(15 == this.imei.length){
				this.btnAddDisable = false;
			}else{
				this.btnAddDisable = true;
			}
		},
		regDev(){
			console.log("regDev URL:"+this.globalVal.default_url.devReg);
			uni.showLoading({
				title: '注册中...',
				mask: false
			});
			var errcode = 1;
			var errmsg = "失败";
			uni.request({
				url: this.globalVal.default_url.devReg,
				method: 'POST',
				data: {
					imei:this.imei
				},
				success: res => {
					console.log(res);
					if(200 == res.statusCode){
						errcode = res.data.errcode;
						if(0 == errcode){
							errmsg = "注册成功";
							this.devid = res.data.deviceId;
							this.btnInfoDisable = false;
						}else{
							errmsg = res.data.errmsg;
						}
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
					uni.showToast({
						title: errmsg,
						icon:"none",
						duration:2500
					});
				}
			});
		}
		
	}
}
